/**
 * all utility css classes
 */
.min-h-dvh {
  min-height: 100vh;
  min-height: 100dvh;
}

.flex-middle {
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes blockblog-top-progress {
  0% {
    left: 0;
  }
  100% {
    left: calc(100% - 100px);
  }
}
:root {
  --blockblog-primary-r: 255;
  --blockblog-primary-g: 143;
  --blockblog-primary-b: 0;
  --blockblog-primary-alpha: 100%;
  --blockblog-primary: rgb(var(--blockblog-primary-r) var(--blockblog-primary-g) var(--blockblog-primary-b) / var(--blockblog-primary-alpha));
}

/* poppins-regular - latin */
@font-face {
  font-display: swap;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/poppins-v23-latin-regular.woff2") format("woff2"), url("../fonts/poppins-v23-latin-regular.ttf") format("truetype");
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  --blockblog-primary-alpha: 10%;
  --blockblog-primary: rgb(var(--blockblog-primary-r) var(--blockblog-primary-g) var(--blockblog-primary-b) / var(--blockblog-primary-alpha));
  background-color: var(--blockblog-primary);
  font-family: "Poppins";
}

.blockblog-container {
  --blockblog-container-size: 1080px;
  max-inline-size: var(--blockblog-container-size);
  margin: 0 auto;
  margin-block: 20px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 14px;
}
.blockblog-container h2 {
  font-size: 20px;
  color: rgb(0, 0, 0);
  margin-bottom: 10px;
}
.blockblog-container.archive {
  grid-template-columns: repeat(auto-fill, 300px);
}
.blockblog-container.archive .blockblog-single {
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
  position: relative;
  padding: 10px;
}
.blockblog-container.archive .blockblog-single img {
  object-fit: cover;
  width: 100%;
}
.blockblog-container.archive .blockblog-single .post-meta {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 10px;
}
.blockblog-container.archive .blockblog-single .post-author-link,
.blockblog-container.archive .blockblog-single .post-category-link {
  color: rgb(0, 0, 0);
}
.blockblog-container.archive .blockblog-single .post-title,
.blockblog-container.archive .blockblog-single .post-excerpt {
  color: rgba(0, 0, 0, 0.8);
  text-decoration: none;
}
.blockblog-container.archive .blockblog-single:hover, .blockblog-container.archive .blockblog-single:focus-within {
  background: rgba(0, 0, 0, 0.05);
}
.blockblog-container.archive .blockblog-single:hover .post-title,
.blockblog-container.archive .blockblog-single:hover .post-excerpt,
.blockblog-container.archive .blockblog-single:hover .post-thumbnail,
.blockblog-container.archive .blockblog-single:hover .post-meta, .blockblog-container.archive .blockblog-single:focus-within .post-title,
.blockblog-container.archive .blockblog-single:focus-within .post-excerpt,
.blockblog-container.archive .blockblog-single:focus-within .post-thumbnail,
.blockblog-container.archive .blockblog-single:focus-within .post-meta {
  filter: blur(1px);
}
.blockblog-container.archive .blockblog-single:hover .post-title:hover, .blockblog-container.archive .blockblog-single:hover .post-title:focus-within,
.blockblog-container.archive .blockblog-single:hover .post-excerpt:hover,
.blockblog-container.archive .blockblog-single:hover .post-excerpt:focus-within,
.blockblog-container.archive .blockblog-single:hover .post-thumbnail:hover,
.blockblog-container.archive .blockblog-single:hover .post-thumbnail:focus-within,
.blockblog-container.archive .blockblog-single:hover .post-meta:hover,
.blockblog-container.archive .blockblog-single:hover .post-meta:focus-within, .blockblog-container.archive .blockblog-single:focus-within .post-title:hover, .blockblog-container.archive .blockblog-single:focus-within .post-title:focus-within,
.blockblog-container.archive .blockblog-single:focus-within .post-excerpt:hover,
.blockblog-container.archive .blockblog-single:focus-within .post-excerpt:focus-within,
.blockblog-container.archive .blockblog-single:focus-within .post-thumbnail:hover,
.blockblog-container.archive .blockblog-single:focus-within .post-thumbnail:focus-within,
.blockblog-container.archive .blockblog-single:focus-within .post-meta:hover,
.blockblog-container.archive .blockblog-single:focus-within .post-meta:focus-within {
  filter: blur(0);
}
.blockblog-container.archive .blockblog-single:hover .read-more, .blockblog-container.archive .blockblog-single:focus-within .read-more {
  opacity: 1;
  pointer-events: auto;
}
.blockblog-container.archive .blockblog-single .read-more {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
  font-size: 14px;
  color: rgb(0, 0, 0);
  --blockblog-primary-alpha: 100%;
  --blockblog-primary: rgb(
    var(--blockblog-primary-r) var(--blockblog-primary-g)
      var(--blockblog-primary-b) / var(--blockblog-primary-alpha)
  );
  background-color: var(--blockblog-primary);
  padding: 8px 12px;
  text-decoration: none;
  opacity: 0;
  pointer-events: none;
}
.blockblog-container.archive .blockblog-single .read-more:hover {
  text-decoration: underline;
}
.blockblog-container.posts-not-found {
  text-align: center;
}
.blockblog-container:not(.archive) .post-thumbnail {
  width: min(500px, 100%);
  aspect-ratio: 16/9;
  margin-inline: auto;
  object-fit: contain;
  object-position: center;
  border-radius: 8px;
  margin-bottom: 0.8rem;
}
.blockblog-container:not(.archive) .post-nav-links {
  background: #fff;
  padding: 10px;
  margin-block: 8px;
}
.blockblog-container:not(.archive) .post-nav-links a,
.blockblog-container:not(.archive) .post-nav-links span {
  background: rgba(85, 85, 85, 0.3215686275);
  padding: 4px 8px;
  font-size: 14px;
  color: #000;
  font-weight: 400;
}
.blockblog-container:not(.archive) .post-nav-links a {
  border: 1px solid #000;
  text-decoration: none;
  font-weight: 700;
}
.blockblog-container:not(.archive) .post-content p {
  text-wrap: wrap;
  word-break: break-all;
  white-space: pre-wrap;
}
.blockblog-container:not(.archive) .post-content .language {
  overflow: auto;
  display: block;
  max-inline-size: var(--blockblog-container-size);
}

.blockblog-comments-area {
  margin-top: 20px;
}
.blockblog-comments-area .logged-in-as {
  margin-block: 10px;
}
.blockblog-comments-area .logged-in-as .required-field-message {
  display: block;
  font-weight: 600;
}
.blockblog-comments-area .submit {
  padding: 8px 12px;
  margin-top: 10px;
  width: fit-content;
  margin-left: auto;
  display: block;
  border: 0;
  color: #fff;
  border-radius: 4px;
  --blockblog-primary-alpha: 100%;
  --blockblog-primary: rgb(
    var(--blockblog-primary-r) var(--blockblog-primary-g)
      var(--blockblog-primary-b) / var(--blockblog-primary-alpha)
  );
  background-color: var(--blockblog-primary);
}
.blockblog-comments-area .comment-reply-title {
  font-size: 1rem;
}
.blockblog-comments-area .comment-list {
  list-style-type: none;
}
.blockblog-comments-area .comment-list .comment {
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
  padding: 10px 14px;
  margin-block: 8px;
}
.blockblog-comments-area .comment-list .comment .children {
  margin-left: 20px;
  border-left: 2px solid rgba(0, 0, 0, 0.1);
  padding-left: 10px;
  list-style-type: none;
}
.blockblog-comments-area .comment-list .comment .comment-meta {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-block: 6px 10px;
}
.blockblog-comments-area .comment-list .comment .comment-meta .comment-awaiting-moderation {
  margin-top: 6px;
  color: #808080;
  font-size: 12px;
  display: block;
  width: 100%;
}
.blockblog-comments-area .comment-list .comment .comment-meta .comment-author {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
}
.blockblog-comments-area .comment-list .comment .comment-meta .comment-author img {
  border-radius: 50%;
  width: 34px;
  height: 34px;
  object-fit: cover;
}
.blockblog-comments-area .comment-list .comment .comment-meta .comment-author a {
  text-decoration: none;
  color: #000;
}
.blockblog-comments-area .comment-list .comment .comment-meta .comment-metadata a {
  text-decoration: none;
  color: #000;
}
.blockblog-comments-area .comment-list .comment .comment-meta .comment-metadata time {
  font-size: 12px;
}
.blockblog-comments-area .comment-list .comment .comment-meta .comment-metadata span a {
  text-decoration: underline;
  font-size: 14px;
}
.blockblog-comments-area .comment-list .comment .reply a {
  display: block;
  width: fit-content;
  margin-left: auto;
  color: #000;
  font-size: 14px;
}
.blockblog-comments-area .comment-form label {
  font-size: 14px;
}
.blockblog-comments-area textarea,
.blockblog-comments-area input {
  border-radius: 10px;
}
.blockblog-comments-area .comment-form-cookies-consent {
  display: flex;
  gap: 4px;
  align-items: center;
  margin-block: 8px;
}
.blockblog-comments-area .comment-respond:has(.must-log-in) {
  background: #fff;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}

header {
  --blockblog-primary-alpha: 80%;
  --blockblog-primary: rgb(var(--blockblog-primary-r) var(--blockblog-primary-g) var(--blockblog-primary-b) / var(--blockblog-primary-alpha));
  background-color: var(--blockblog-primary);
}
header .blockblog-top-progress {
  width: 100%;
  height: 3px;
  position: relative;
  top: 2px;
  left: 0;
  display: flex;
  opacity: 1;
  background: rgb(255, 163, 46);
}
header .blockblog-top-progress::before {
  display: none;
  content: "";
  width: 100px;
  height: 100%;
  position: absolute;
  background: #fff;
  animation: blockblog-top-progress 2000ms ease infinite;
}
header .blockblog-top-progress.loading::before {
  display: block;
}
header .blockblog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  max-inline-size: 1080px;
  margin: 0 auto;
}
header .blockblog-header .logo-and-nav {
  display: flex;
  column-gap: 20px;
  align-items: center;
}
header .blockblog-header .logo-and-nav .logo {
  font-size: 20px;
}
header .blockblog-header .logo-and-nav .logo a {
  text-decoration: none;
  color: rgb(0, 0, 0);
  font-weight: 600;
}
header .blockblog-header .logo-and-nav .nav-menu ul {
  display: flex;
  list-style: none;
  column-gap: 8px;
}
header .blockblog-header .logo-and-nav .nav-menu ul a {
  text-decoration: none;
  font-size: 16px;
  color: rgb(0, 0, 0);
  position: relative;
}
header .blockblog-header .logo-and-nav .nav-menu ul a::before {
  content: "";
  width: 100%;
  height: 1.5px;
  background: red;
  position: absolute;
  bottom: -2px;
  left: 0;
  transform: scaleX(0);
  transition: transform 150ms ease;
}
header .blockblog-header .logo-and-nav .nav-menu ul a:hover::before {
  width: 100%;
  transform: scaleX(1);
}
header .blockblog-header .search-and-profile {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 8px;
}
header .blockblog-header .search-and-profile .avatar {
  display: flex;
  align-items: center;
}
header .blockblog-header .search-and-profile .avatar .blockblog-menu-toggler {
  display: none;
}
header .blockblog-header .search-and-profile form div {
  display: flex;
  gap: 0;
  height: 35px;
  width: fit-content;
  --border-radius: 8px;
}
header .blockblog-header .search-and-profile form div input {
  padding: 0 10px;
  border-top-left-radius: var(--border-radius);
  border-bottom-left-radius: var(--border-radius);
  border-width: 1px;
  background: #fff;
  font-size: 14px;
  font-weight: 400;
}
header .blockblog-header .search-and-profile form div button {
  width: 35px;
  border-width: 1px;
  border-top-right-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
  background: #fff;
  cursor: pointer;
}
header .blockblog-header .search-and-profile .blockblog-profile-link {
  display: flex;
}
header .blockblog-header .search-and-profile .blockblog-profile-link img {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
  cursor: pointer;
}

.blockblog-footer {
  background-color: #140c01;
  color: #fff;
  padding: 0;
}
.blockblog-footer .blockblog-footer-inner {
  padding: 36px 16px;
  max-inline-size: 1080px;
  padding-inline: 14px;
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  row-gap: 8px;
  justify-content: space-between;
  align-items: center;
}
.blockblog-footer .blockblog-footer-inner ul {
  list-style: none;
}
.blockblog-footer .footer-section.footer-section-menus {
  display: flex;
  flex-flow: row wrap;
  gap: 22px;
}
.blockblog-footer .footer-section.footer-section-menus a {
  color: #9191ff;
  text-decoration: none;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 2px;
}
.blockblog-footer .footer-section.footer-section-menus a:hover {
  text-decoration: underline;
}
.blockblog-footer .footer-section.footer-section-menus a span {
  font-size: 18px;
  width: fit-content;
  height: fit-content;
}
.blockblog-footer .footer-section.footer-section-menus li {
  margin-bottom: 4px;
}
.blockblog-footer .footer-section .footer-subsection {
  width: 200px;
}
.blockblog-footer .footer-section.footer-owner-data {
  display: flex;
  flex-direction: column;
  gap: 18px;
}
.blockblog-footer .footer-section.footer-owner-data h3 {
  margin-bottom: 10px;
  font-size: 22px;
}
.blockblog-footer .footer-section.footer-owner-data p {
  font-size: 12px;
  margin-bottom: 2px;
  color: #ccc;
}
.blockblog-footer .footer-section .footer-search .search-actions-wrapper {
  display: flex;
  gap: 8px;
}
.blockblog-footer .footer-section .footer-search input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 4px;
}
.blockblog-footer .footer-section .footer-search button {
  padding: 8px 12px;
  background-color: #001118;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  margin-top: 4px;
}
.blockblog-footer .footer-socials ul {
  display: flex;
  flex-flow: row wrap;
  gap: 8px;
  align-items: center;
}
.blockblog-footer .footer-socials ul li {
  list-style: none;
  display: inline-block;
}
.blockblog-footer .footer-socials ul li a {
  color: #fff;
  text-decoration: none;
  padding: 8px;
  display: flex;
  border-radius: 4px;
  border: 0.5px solid #000;
  position: relative;
}
.blockblog-footer .footer-socials ul li a:hover, .blockblog-footer .footer-socials ul li a:focus {
  background-color: #000;
}
.blockblog-footer .footer-socials ul li a:hover::before {
  transform: translateY(0);
  opacity: 1;
}
.blockblog-footer .footer-socials ul li a::before {
  content: "Open Link";
  position: absolute;
  top: -34px;
  left: 0;
  border-radius: 4px;
  background: #000;
  width: 100px;
  padding: 4px;
  text-align: center;
  font-size: 14px;
  opacity: 0;
  transform: translateY(8px);
  transition: transform 0.2s ease, opacity 0.3s ease;
  pointer-events: none;
}
.blockblog-footer .copyright {
  text-align: center;
  padding: 8px 0;
  font-size: 14px;
  color: #fff;
  background-color: #666;
}

.screen-reader-text {
  display: none;
}

@media only screen and (max-width: 768px) {
  header .blockblog-header {
    flex-direction: column;
    align-items: flex-end;
    gap: 12px;
  }
  header .blockblog-header .logo-and-nav {
    width: 100%;
  }
}
@media only screen and (max-width: 500px) {
  header .blockblog-header {
    flex-direction: row;
    align-items: center;
    position: relative;
    z-index: 10;
  }
  header .blockblog-header .logo-and-nav .logo {
    font-size: 16px;
  }
  header .blockblog-header .logo-and-nav .blockblog-nav.nav-shown ul {
    display: flex;
  }
  header .blockblog-header .logo-and-nav .blockblog-nav ul {
    flex-direction: column;
    position: absolute;
    top: 55px;
    --blockblog-primary-alpha: 80%;
    --blockblog-primary: rgb(var(--blockblog-primary-r) var(--blockblog-primary-g) var(--blockblog-primary-b) / var(--blockblog-primary-alpha));
    background-color: var(--blockblog-primary);
    width: 100%;
    left: 0;
    padding: 0 10px;
    display: none;
  }
  header .blockblog-header .logo-and-nav .blockblog-nav ul li a {
    padding: 8px 0;
    display: block;
    width: 100%;
    height: 100%;
  }
  header .blockblog-header .search-and-profile .blockblog-search input, header .blockblog-header .search-and-profile .blockblog-search button {
    height: 35px;
  }
  header .blockblog-header .search-and-profile .blockblog-search input {
    width: 130px;
  }
  header .blockblog-header .search-and-profile .avatar img {
    display: none;
  }
  header .blockblog-header .search-and-profile .avatar .blockblog-menu-toggler {
    display: block;
  }
}
@media only screen and (max-width: 500px) {
  .blockblog-container {
    --blockblog-container-size: 360px;
    padding-inline: 20px;
  }
  .blockblog-container.archive {
    grid-template-columns: repeat(1, 1fr);
  }
  .blockblog-container:not(.archive) h1 {
    font-size: 1.5rem;
  }
  .blockblog-container:not(.archive) .blockblog-single-post,
  .blockblog-container:not(.archive) .blockblog-single-page {
    max-width: 390px;
    white-space: pre-line;
  }
  .blockblog-container:not(.archive) .language {
    font-size: 12px;
  }
}
@media only screen and (max-width: 390px) {
  .blockblog-container:not(.archive) {
    padding-inline: 10px;
  }
  .blockblog-container:not(.archive) .blockblog-single-page,
  .blockblog-container:not(.archive) .blockblog-single-post {
    max-width: 370px;
  }
}
@media only screen and (max-width: 360px) {
  .blockblog-container:not(.archive) {
    padding-inline: 10px;
  }
  .blockblog-container:not(.archive) .blockblog-single-page,
  .blockblog-container:not(.archive) .blockblog-single-post {
    max-width: 340px;
  }
}
@media only screen and (max-width: 1080px) {
  .blockblog-container {
    --blockblog-container-size: 800px;
    padding-inline: 20px;
  }
}
@media only screen and (max-width: 800px) {
  .blockblog-container {
    --blockblog-container-size: 500px;
    padding-inline: 20px;
  }
}